.wrapper {
    display: flex;
    align-items: center;
    height: 48px;
    padding-right: 24px;
    background: var(--gs-0);
    flex-direction: row;
    flex-wrap: nowrap;
    border-bottom: 1px solid var(--gs-20);
}

.logoImg {
    object-fit: contain;
    width: 110px;
}

.logo {
    border-right: 1px solid var(--gs-20);
    width: 230px;
}

.logoWrapper {
    height: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.profile {
    font-size: 16px;
    margin-left: auto;
}

.menu {
    font-size: 16px;
    border: none;
}
